/* src/styles.css */

/* General styling */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
}

/* App container */
.app {
    text-align: center;
    padding: 20px;
}

/* Search bar styling */
.search-bar input {
    padding: 10px;
    width: 80%;
    margin: 20px 0;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

/* Movie list */
.movie-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* Flexibly fill the space with small movie items */
    gap: 20px;
    padding: 0 10px;
}

.movie-item {
    background-color: white;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.movie-poster {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 5px;
}

.movie-details h3 {
    font-size: 18px;
    color: #333;
    margin-top: 10px;
}

.movie-details p {
    font-size: 14px;
    color: #777;
}

/* Help page */
.help-search {
    text-align: center;
    margin-top: 20px;
}
